*{
margin: 0;
padding: 0;
box-sizing: border-box;
font-family: 'fira sans',sans-serif;
}
body{
   background-color: rgb(26, 27, 46);
   color: rgb(236, 228, 228);
}
.App{
  padding: 32px;
/* text-align: center; */

}

h1{
  font-size: 40px;
font-weight: 700;
margin-bottom: 32px;

}
h4{
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 400;
  margin-bottom: 16px;
}
.todo{
  position: relative;
  background-color:rgb(0, 0, 0) ;
  padding: 16px;
  border-radius: 16px;
  display: flex;
  text-align: center;
  transition: 0.5s;
  cursor: pointer;
  margin-bottom: 16px;
  
}

.todo:hover{
opacity: 0.75;

}

.todo .checkbox{
  width: 20px;
  height: 20px;
  margin-right: 16px;
  border-radius: 999px;
background-color: rgb(26, 27, 46);
transition:0.1s ;
}
.todo.is-completed .checkbox{
  background-color: green;
  background-image: linear-gradient(to bottom,green,rgb(7, 7, 7));
}

.todo .text{
  font-size: 20px;
}

.todo.is-completed .text{
  text-decoration: line-through;
}
.todo .delete-todo{
  position: absolute;
  top:50%;
  right:16px ;
  transform: translateY(-50%);

  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  color: rgb(255, 255, 255);
  background-color: rgb(158, 12, 12);
  border-radius: 50%;
}


.addPopup{
  position: fixed;
  bottom: 32px;
  right: 32px;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 64px;
  height: 64px;
  border-radius: 999px;
  font-size: 28px;
  font-weight:900 ;
  color: white;
  background-image: linear-gradient(to bottom right ,rgb(128, 85, 6),rgb(255, 35, 71));
}

.popup{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);

  width:100% ;
  max-width: 400px;
  background-color: wheat;
  padding: 32px;
  border-radius: 16px;
  box-shadow: 0px 6px 34px black;
}
.closePopup{
  position: absolute;
  top: 16px;
  right: 16px;
  width: 20px;
  height: 20px;
  font-size: 20px;
  display:flex;
  justify-content: center;
  align-items: center;
  background-color:red;
  border-radius: 50%;
  cursor:pointer;
}

.popup h3{
 color: rgb(26, 27, 46);
 margin-bottom: 16px;
 font-weight: 600;
 text-transform: uppercase;
}

.add-todo-input{
  appearance: none;
  border: none;
  outline: none;
  background-color: #fff;
  padding: 16px;
  border-radius: 16px;
  box-shadow: 0px 2px 24px black ;

}
.button{
  padding: 16px 32px;
  border-radius: 99px;
  background-image: linear-gradient(to bottom right ,rgb(128, 85, 6),rgb(255, 35, 71));
  display: inline-block;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 18px;
  margin-top: 16px;
  text-align: center;
  cursor: pointer;
}